	module.exports = {"data":{"markdownRemark":{"html":"<p>It is a self-made music list. Since I started to work on the eastern arrangement, since 2004, those who are looking for the eastern side please go to the menu of the corresponding age. (Before that it is a kind of original song and arrangement mainly related to DDR · BM)</p>\n<h2>Recommended environment / attention</h2>\n<ul>\n<li>Basically, the music format is MP3 and WMA (preview only), but some songs are also mixed with MIDI and BMS.</li>\n<li>MIDI songs are \"XG\" songs after YAMAHA MU 50, \"GS\" songs can be happy when asked after Roland SC - 88 Pro, maybe.</li>\n<li>BMS music requires 7 lines BME compatible players to play.</li>\n</ul>","frontmatter":{"cover":null,"date":"2007-12-31T00:00:00+09:00","strDate":"2007/12/30","title":"About the page of homebrew music"}}},"pathContext":{"path":"/en/blog/2007/12/31-自作楽曲のページについて/","langKey":"en"}}

